import classNames from "classnames";
import CheckBox from "component/CheckBox";
import Fold from "component/Icon/Fold";
import Left from "component/Icon/Left";
import Star from "component/Icon/Start";
import List from "component/List";
import Loading from "component/Loading";
import { isArray } from "lodash";
import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import styles from './index.less'



const travel = (node: any, cb: any) => {
    cb(node);
    if (node.child) {
        travel(node.child, cb)
    }

    if (node.next) {
        travel(node.next, cb)
    }
}

const travelParent = (node: { parent: any; }, cb: { (n: any): void; (arg0: any): void; }) => {
    cb(node);
    if (node.parent) {
        travelParent(node.parent, cb)
    }
}

const getParentSelect = (node: any, fn: any) => {
    if (node.isChecked) {
        fn(node)
        node.next && getParentSelect(node.next, fn)
    } else {
        node.child && getParentSelect(node.child, fn);
        node.next && getParentSelect(node.next, fn)
    }
}
const getLeafSelect = (node: any, fn: any) => {
    if (!node.canExpand && node.isChecked) {
        fn(node);
    }
    node.child && getLeafSelect(node.child, fn);
    node.next && getLeafSelect(node.next, fn)
}

const Tree = (props: any) => {
    const { 
        data, isMulti, type = 'leaf', 
        nodeProps, 
        renderNode, 
        onCheck,
        onLoad,
        className,
    } = props;
    const lastRootNodeRef = useRef();
    const [list, setList] = useState([])
    const rootNode = useMemo(() => {
        if (data.length <= 0) {
            return undefined;
        }

        const createNode = (
            nodeData: any,
            lastNode?: any,
            parent?: any,
            level = 0,
        ) => {
            const {
                children, key, isChecked = false, isDisabled = false,
                isLoaded = true,
            } = nodeProps(nodeData) as any;

            //加上🇺每个node 的状态 跟 parent
            const node = {
                value: nodeData,
                key,
                level,
                isExpand: false,
                canExpand: children !== undefined,
                isLoaded, // 是否已经假装数据
                isChecked,
                isDisabled,
                isIndeterminate: false,
                loading: false,
                parent, // 第一次递归是空，第二次就带上上次的节点
            } as any;
            // 因为每次 loading 都是false 
            // 不知道为什么 上次 什么逻辑  这里是为了记录 loading， 是否加载完成，未来就是true
            if (lastNode && lastNode.key === node.key) {
                node.isExpand = lastNode.isExpand;
                if (node.isLoaded === false) {
                    node.loading = lastNode.loading;
                }
            }

            // 父级选中  子级选中  如果它的父级北选中 ，则当前孩子选中
            if (parent && parent.isChecked) {
                node.isChecked = true;
            }
            // 有孩子数据继续创建
            if (isArray(children) && children.length > 0) {

                let cNode: any;
                let lastCNode = lastNode ? lastNode.child : undefined;
                let checkedChildCount = 0; // 子孩子是否全部选中
                for (let i = 0; i < children.length; i += 1) {

                    // 递归创建子级
                    const thisNode = createNode(children[i], lastCNode, node, level + 1);
                    // 最后一个孩子

                    // 第二次呢 回来的时候 cNode 已经被复制  回到第二个的时候 
                    if (cNode === undefined) {
                        cNode = thisNode;
                        node.child = cNode;
                    } else {
                        cNode.next = thisNode;
                        cNode = thisNode;
                    }

                    lastCNode = lastCNode ? lastCNode.next : undefined;
                    // 如何当前元素是选中的
                    if (cNode.isChecked) {
                        node.isIndeterminate = true;
                        checkedChildCount += 1;
                    }
                    // 向上的都 变
                    if (node.isIndeterminate && node.parent) {
                        travelParent(node.parent, (n: any) => {
                            n.isIndeterminate = true;
                        })
                    }

                    // 看子级是否选中
                    if (checkedChildCount === children.length) {
                        node.isChecked = true;
                    } 

                    // 不能要
                    // else {
                    //     node.isChecked = false
                    // }
                }
            }
            return node;
        }
        let lastCurrent = lastRootNodeRef.current as any;

        let root = createNode(data[0], lastCurrent);

        let current = root;
        for (let i = 1; i < data.length; i++) {
            const item = data[i];
            if (item) {
                lastCurrent = lastCurrent ? lastCurrent.next : undefined;
                current.next = createNode(item, lastCurrent)
                current = current.next;
            }
        }
        lastRootNodeRef.current = root;

        return root;
    }, [nodeProps])

    const createList = () => {
        if (rootNode) {
            const result = [] as any;
            travel(rootNode, (node: { parent: { isExpand: any; }; }) => {
                if (!node.parent || (node.parent && node.parent.isExpand)) {
                    result.push(node)
                }
            })
            setList(result)
        }
    }

    useEffect(() => {

        createList()
    }, [rootNode]);

    const handleCheck = (checkedNode: any, checked: boolean) => {
        checkedNode.isChecked = checked;
        if (!isMulti && onCheck) {
            onCheck([checkedNode.value])
            return;
        }
        // checkedNode.isChecked = checked;
        if (checkedNode.child) {
            travel(checkedNode.child, (n: any) => {
                n.isChecked = checked
            })
        }
        if (checkedNode.parent && !checked) {
            travelParent(checkedNode.parent, (n: any) => {
                n.isChecked = false
            })
        }
        // 为真的时候
        if (checkedNode.parent && checked) {
            travelParent(checkedNode.parent, (node: any) => {
                let { child } = node as any;
                let allParent = true;
                while (child) {
                    allParent = child.isChecked && allParent;
                    if (!child.isChecked) break;
                    child = child.next;
                }
                node.isChecked = allParent
            })
        }

        // 选中的元素
        const res = [] as any;
        const root = rootNode;
        if (type === 'leaf') {
            getLeafSelect(root, (n: any) => {
                res.push(n)
            })
        } else {
            getParentSelect(root, (n: any) => {
                res.push(n)
            })
        }
        // console.log(res, 'res', type)
        if (onCheck) { 
            onCheck([...res].map(item => item.value))
        }

        createList();
    }

    const renderItem = (index: number) => {

        const item = list[index] as any;
        const result = renderNode(item);
        let icon;
        if (item.child && result.icon) {
            icon = (
                <span className={styles.placeholder}>
                    {result.icon}
                </span>
            );
        } else if (item.child === undefined) {
            icon = (
                <span className={styles.placeholder}>
                    {result.icon}
                </span>
            );
        }
        const toggleExpand = (e?: any) => {
            // 是子节点 
            if (item.canExpand === false || item.loading) {
                return;
            }
            if (item.isExpand) {
                item.isExpand = false;
                if (item.child) {
                    travel(item.child, (n: any) => {
                        n.isExpand = false
                    })
                }
            } else {
                if (onLoad && item.isLoaded === false) { // 加载模式下时启动加载
                    item.loading = true;
                    onLoad(item.value);
                }
                item.isExpand = true;
            }
            if (e) {
                e.stopPropagation();
            }
            createList();
        }
        const toggleCheck = (e: { stopPropagation: () => void; }) => {
            const checked = !item.isChecked;
            handleCheck(item, checked);
            e.stopPropagation();
            return
        }
        // if (isMulti) {
        return (
            <div
                className={classNames(styles.row, {
                    // [styles.active]: isActive
                })}
                onClick={(e) => {
                    if (isMulti) {
                        toggleExpand()
                    } else {
                        toggleCheck(e)
                    }

                }}
                style={{ paddingLeft: item.level * 20 }}
            >
                {/* {
                    item.canExpand ? <span
                        className={styles.iconContainer}
                        onClick={toggleExpand}
                    >
                        <Left className={classNames(styles.expandIcon, {
                            [styles.rotate]: item.isExpand
                        })} />
                        <Fold className={styles.fold} />
                    </span> : <Star className={styles.star} />
                } */}
                {
                    item.canExpand ? <span
                        className={styles.iconContainer}
                        onClick={toggleExpand}
                    >
                        {
                            item.loading ? <Loading className={styles.loading} /> : <Left className={classNames(styles.expandIcon, {
                                [styles.rotate]: item.isExpand
                            })} />
                        }
                       
                    </span> : null
                }
                {
                    icon
                }
                {result.content}
                {/* <span> {item.value.id}</span> */}
                {
                    isMulti ? <span className={styles.checkbox}>
                        <CheckBox
                            options={undefined}
                            checked={item.isChecked}
                            // onChange={() => { }}
                            // indeterminate={item.isIndeterminate}
                            onClick={toggleCheck}
                        />
                    </span> : null
                }

            </div>
        )

    }
    return (

        <List list={list} render={renderItem} className={classNames(styles.listContainer, className) } />

    )

}

export default Tree;