export default "import Fold from \"component/Icon/Fold\";\nimport Star from \"component/Icon/Start\";\nimport TreeSelect from \"component/TreeSelect\";\nimport { useState } from \"react\";\nimport styles from './index.less'\nconst data = [\n    {\n        id: '1-0',\n        array: [\n            {\n                id: '1-0-1',\n                array: [\n                    {\n                        id: '1-0-1-1',\n                        array: undefined,\n                    },\n                ],\n            },\n            {\n                id: '1-0-2',\n                array: undefined,\n            },\n            {\n                id: '1-0-3',\n                array: undefined,\n            },\n        ],\n\n    },\n\n    {\n        id: '1-1',\n        array: [\n            {\n                id: '1-1-1',\n                array: [\n                    {\n                        id: '1-1-1-1',\n                        array: undefined,\n                    },\n                ],\n            },\n            {\n                id: '1-1-2',\n                array: undefined,\n            },\n            {\n                id: '1-1-3',\n                array: undefined,\n            },\n        ],\n\n    },\n\n    {\n        id: '1-2',\n        array: [\n            {\n                id: '1-2-1',\n                array: [\n                    {\n                        id: '1-2-1-1',\n                        array: undefined,\n                    },\n                ],\n            },\n            {\n                id: '1-2-2',\n                array: undefined,\n            },\n            {\n                id: '1-2-3',\n                array: undefined,\n            },\n        ],\n\n    },\n];\nconst TreeSelectBaseView = () => {\n    const [checkedItems, setCheckedItems] = useState([]);\n    const nodeProps = ((item: any) => ({\n        key: item.id,\n        children: item.array,\n        isChecked: checkedItems.some((i: any) => i === item.id),\n    }));\n\n    const renderNode = (item: { value: { id: boolean; array: any; }; }) => ({\n        content: <span className={styles.content}>{item.value.id}</span>,\n        icon: item.value.array ? <Fold className={styles.folder} /> : <Star className={styles.star} />,\n    })\n    return (\n        <div>\n            <TreeSelect\n                data={data}\n                nodeProps={nodeProps}\n                renderNode={renderNode}\n                onCheck={setCheckedItems}\n                value={checkedItems}\n             />\n        </div>\n    )\n}\n\nexport default TreeSelectBaseView;";