

import styles from './index.less'
import CursorInput from 'component/CursorInput';
import ClearIcon from 'component/Icon/ClearIcon';
import Popover from 'component/Popover'
import { useState } from 'react'
import Tree from 'component/Tree';


const traveData = (node: any, cb: any) => {
    if (node) {
        let task = [...node];
        while (task.length) {
            const item = task.pop();
            if (item) {
                cb(item);
                if (item.array && item.array.length) {
                    task = task.concat([...item.array]);
                }
            }
        }

    }

}

const getValue = (node: any, ids: any) => {
    let res = [] as any;
    traveData(node, (n: { id: string; }) => {
        if (ids.includes(n.id)) {
            res.push({ key: n.id, content: n.id },)
        }
    })

    return res
}

const TreeSelect = (props: any) => {
    const {
        value = [],
        data = [], nodeProps = () => { },
        renderNode,
        onCheck,
    } = props;
    const [visible, setVisible] = useState(false);
    const handleVisible = () => {
        setVisible(true)
    }

    const handleCheck = (item: any) => {
        const ids = item.map((sub: any) => sub.id)
        onCheck(ids)
    }
    const handleDeleInput = (item: any) => {
        const ids = item.map((sub: any) => sub.key);
        onCheck(ids)

    }
    return (
        <Popover
            content={<div className={styles.container}>
                <Tree
                    renderNode={renderNode}
                    nodeProps={nodeProps}
                    data={data}
                    isMulti={true}
                    type='parent'
                    className={styles.treeContainer}
                    onCheck={handleCheck}
                ></Tree>
            </div>}
            placeholder="BL"
            visible={visible}
            trigged="popover"
            isArrow={false}
            onVisibleChange={() => { setVisible(false) }}
            overlayClass={styles.overlayClass}
        >
            <div className={styles.cursorInput} onClick={handleVisible}>
                <CursorInput
                    selectedOptions={getValue(data, value)}
                    setSelectedOptions={handleDeleInput}
                    placeHolder="请点击选择"
                ></CursorInput>
                {
                    <span className={!value.length ? styles.noShow : styles.clearIcon} onClick={() => onCheck([])}>
                        <ClearIcon />
                    </span>
                }

            </div>
        </Popover>

    )
}

export default TreeSelect